import qs from "qs";
import axios from "axios";
import { router } from "@/router/router.js";
import { message } from "ant-design-vue";

const instance = axios.create({
    baseURL: "/api",
    timeout: 5000,
    withCredentials: true,
});

// 请求拦截器
instance.interceptors.request.use(
    (config) => {
        config.headers["x-requested-with"] = "XMLHttpRequest";
        // token
        config.headers["Authorization"] = `Bearer ${sessionStorage.getItem(
            "token"
        )}`;
        return config;
    },
    async (error) => {
        // 对请求错误做些什么
        return Promise.reject(error);
    }
);

// 响应拦截器
instance.interceptors.response.use(
    (res) => {
        return res.data;
    },
    async (error) => {
        message.error(error.response.data.message);
        // token 过期 jwt expired
        if (error.response.data.code === 1025) {
            router.push("/login");
        }
        return Promise.reject(error);
    }
);

export default function (method, url, data = null, config) {
    method = method.toLowerCase();
    if (method === "post") {
        return instance.post(
            url,
            qs.stringify(
                data,
                {
                    indices: false,
                    allowDots: true,
                    arrayFormat: "indices",
                },
                config
            )
        );
    } else if (method === "postalldots") {
        return instance.post(url, qs.stringify(data, { allowDots: true }));
    } else if (method === "postarray") {
        return instance.post(url, qs.stringify(data, { indices: false }));
    } else if (method === "postjson") {
        return instance.post(url, data);
    } else if (method === "get") {
        return instance.get(url, { params: data });
    } else if (method === "delete") {
        return instance.delete(
            url,
            qs.stringify(
                data,
                {
                    indices: false,
                    allowDots: true,
                    arrayFormat: "indices",
                },
                config
            )
        );
    } else if (method === "put") {
        return instance.put(url, data);
    } else if (method === "link") {
        // return apiConfig.baseUrl + url;
    } else if (method === "upload") {
        return instance.post(url, data);
    } else if (method === "getdownload") {
        return instance.get(url, { responseType: "blob" });
    } else {
        console.error("未知的method" + method);
        return false;
    }
}
